import Vue from 'vue'
import Router from 'vue-router'
import floor from '../components/floor.vue'
import room from '../components/room.vue'
// 全局使用router
Vue.use(Router)

export default new Router({
  routes: [ // 配置路由
    {
      path: '/',
      component: floor
    },
    {
      path: '/room',
      component: room
    }
  ]
})



