<template>
  <div class="seat-wrapper">
    <h3>{{roomNum}}房间</h3>
    <div class="inner-seat-wrapper" id="height" ref="innerSeatWrapper">
      <div v-for="row in seatRow" :key="row">
        <!--这里的v-if很重要，如果没有则会导致报错，因为seatArray初始状态为空-->
        <div v-for="col in seatCol" 
          :key="col" class="seat" 
          :style="{width:seatSize+'px',height:seatSize+'px'}">
          <div v-if="seatArray.length!==0" 
            class="inner-seat" 
            @click="handleChooseSeat(row-1,col-1)" 
            :class="seatArray[row-1][col-1]===2?'bought-seat'
            :(seatArray[row-1][col-1]===1?'selected-seat':'unselected-seat')">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/**
 * 6*8的教室
 * */
  export default {
   data () {
       return{
        //座位的二维数组，0为无人，1为选中(绿色),2为已被选座位(红色)
        seatArray: [],
        //座位行数
        seatRow: 6,
        //座位列数
        seatCol: 6,
        //座位尺寸
        seatSize: '',
       }
   },
   props:{
       roomNum:{
           type:String,
           requir:true
       }
   },

   methods:{
       //处理座位选择逻辑
      handleChooseSeat: function (row, col) {
        console.log(this.roomNum)
        let seatValue = this.seatArray[row][col];
        let newArray = this.seatArray;
        //如果是已购座位，直接返回
        if (seatValue === 2) return
        //如果是已选座位点击后变未选
        if (seatValue === 1) {
          newArray[row][col] = 0
        } else if (seatValue === 0) {
          newArray[row][col] = 1
        }
        //必须整体更新二维数组，Vue无法检测到数组某一项更新,必须slice复制一个数组才行
        this.seatArray = newArray.slice();
      },
       //初始座位数组
      initSeatArray: function () {
        //这里通过 refs 获得座位父元素的宽度                           获得在浏览器中 这个元素被渲染的 宽度
        this.seatSize = parseInt(parseInt(window.getComputedStyle(this.$refs.innerSeatWrapper).width, 10) / this.seatCol, 10);
        let theW=window.getComputedStyle(this.$refs.innerSeatWrapper).width
        console.log(theW)
        // document.getElementById("height").style.height=theW
        

        let seatArray = Array(this.seatRow).fill(0).map(() => Array(this.seatCol).fill(0));
        this.seatArray = seatArray;
      },
    },
    mounted:function(){
      // 没用this.initSeatArray() 而是用的 initSeatArray()
      // 搞我
      this.initSeatArray()
    }
  }
</script>

<style lang="less" scoped>
  .seat-wrapper {
    width: 100%;
    height: 500px;
    border: 1px dotted #c5c5c5;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
  }

  .inner-seat-wrapper {
    position: absolute;
    top: 40px;
    width: 100%;
    box-sizing: border-box;
  }

  .seat {
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .inner-seat {
    width: 80%;
    height: 80%;
    cursor: pointer;
  }

  .selected-seat {
    background: url('./../assets/selected.png') center center no-repeat;
    background-size: 100% 100%;
  }

  .unselected-seat {
    background: url('./../assets/unselected.png') center center no-repeat;
    background-size: 100% 100%;
  }

  .bought-seat {
    background: url('./../assets/bought.png') center center no-repeat;
    background-size: 100% 100%;
  }

</style>



