<template>
    <div class="floor">
        <div v-for="floor in floors" :key="floor.id">
            <div class="card-header">{{floor.name}}</div>
            <div class="row">
                <router-link to="/room" 
                class="col btn btn-outline-primary" 
                v-for="room in floor.roomsInThisFloor" 
                :key="room.id" 
                :roomNum="room.name">
                {{room.name}}</router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            floors:[
                {
                    id:1,
                    name:"一楼",
                    roomsInThisFloor:[{
                        id : 1,
                        name:"101"
                        },{
                        id : 2,
                        name:"102"
                        },{
                        id : 3,
                        name:"103"
                        },
                    ]
                },
                {
                    id:2,
                    name:"二楼",
                    roomsInThisFloor:[{
                        id : 1,
                        name:"201"
                        },{
                        id : 2,
                        name:"202"
                        },{
                        id : 3,
                        name:"203"
                        },
                    ]
                },
                {
                    id:3,
                    name:"三楼",
                    roomsInThisFloor:[{
                        id : 1,
                        name:"301"
                        },{
                        id : 2,
                        name:"302"
                        },{
                        id : 3,
                        name:"303"
                        },
                    ]
                },
            ]
        }
    }
}
</script>

<style lang="less" scoped>
   .col{
       margin: 1rem;
       justify-content: space-around;
   }


</style>



